import React, { Component, Fragment } from "react";
import styled from "styled-components";

const ContainerWrap = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  -webkit-perspective: 150px;
  perspective: 150px;

  .womanItem {
    width: 100vw;
    outline: 1px solid ${props => props.theme.green};
    display: flex;
    justify-content: center;
    align-items: center;

    h1 {
      color: ${props => props.theme.green};
      text-transform: uppercase;
    }
  }
`;

const ContainerTop = styled.div`
  height: calc(100vh / 3);
  overflow: hidden;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  outline: 2px solid red;
  .womenWrapper {
    transform: ${props =>
      props.scroll ? `translate3d(0px, -${props.scroll}px, 0px)` : null};
  }
`;

const ContainerMiddle = styled.div`
  height: calc(100vh / 3);
  overflow: hidden;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  outline: 2px solid blue;
`;

const ContainerBottom = styled.div`
  height: calc(100vh / 3);
  overflow: hidden;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  outline: 2px solid yellow;

  .womenWrapper {
    margin-top: calc((-100vh / 3) * 2);
    margin-top: ${props => (props.scroll ? `${props.scroll}px` : "-30vh")};
  }
`;

const InnerWrapTop = styled.div`
  transform: ${props =>
    props.scroll ? `translate3d(0px, -${props.scroll}px, 0px)` : null};
  /* margin-top: calc((-100vh / 3) * 2); */
`;
const InnerWrapMiddle = styled.div`
  margin-top: calc(-100vh / 3);
  transform: ${props =>
    props.scroll ? `translate3d(0px, -${props.scroll}px, 0px)` : null};
  /* margin-top: calc((-100vh / 3) * 2); */
`;
const InnerWrapBottom = styled.div`
  margin-top: calc((-100vh / 3) * 2);
  margin-top: ${props => (props.scroll ? `${props.scroll}px` : null)};
`;

const calcX = x => {
  if (typeof window === "undefined") {
    return 0;
  } else {
    // measure centerpoint
    const winWidth = window.innerWidth;
    const center = winWidth / 2;
    const distFromCenter = x - center;
  }
};

class WomenList extends Component {
  componentDidMount() {
    if (typeof window === "undefined") {
      return 0;
    } else if (typeof window != "undefined") {
      if (document.getElementById("womanList")) {
        const womanListHeight = document.getElementById("womanList")
          .offsetHeight;
        document.body.style.height = womanListHeight + "px";
      }
    }
  }
  render() {
    if (this.props.women) {
      const allWomen = this.props.women.map(woman => {
        return (
          <div className="womanItem" key={woman.id}>
            <h1>{woman.name}</h1>
          </div>
        );
      });

      const containers = (
        <ContainerWrap>
          <ContainerTop>
            <InnerWrapTop
              scroll={this.props.scroll}
              id="womanList"
              className="womenWrapper"
            >
              {allWomen}
            </InnerWrapTop>
          </ContainerTop>
          <ContainerMiddle>
            <InnerWrapMiddle
              scroll={this.props.scroll}
              className="womenWrapper"
            >
              {allWomen}
            </InnerWrapMiddle>
          </ContainerMiddle>
          <ContainerBottom>
            <InnerWrapBottom
              scroll={this.props.scroll}
              className="womenWrapper"
            >
              {allWomen}
            </InnerWrapBottom>
          </ContainerBottom>
        </ContainerWrap>
      );

      return containers;
    } else return null;
  }
}

export default WomenList;
